// 我的一天
<template>
  <div class="box">
    <div class="d1">
      <!-- <van-nav-bar
        left-text="返回"
        right-text="|"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
        :border="border"
      /> -->
      <div>
        <span class="iconfont icon-fanhui" @click="onClickLeft"></span>
        <span class="iconfont icon-zhiwei-quanbuzhiwei"></span>
      </div>
      <p class="p1">我的一天</p>
      <p class="p2">{{ getDate() }}</p>
    </div>
    <div class="d2"></div>
    <div class="d3">
      <van-cell is-link @click="showPopup"
        ><van-button round type="primary" size="large"
          >建议</van-button
        ></van-cell
      >
      <van-popup
        v-model:show="show"
        position="bottom"
        :style="{ height: '40%' }"
      >
        <div>
          <h1>今天</h1>
          <span>你没有任何任务今天到期</span>
          <h2>最近添加</h2>
        </div></van-popup
      >
    </div>
  </div>
</template>
<script>
import { NavBar, Button, Popup } from "vant";
export default {
  name: "page2",
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [Popup.name]: Popup,
  },
  data() {
    return {
      datex: [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ],
      border: false,
      show: false,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push("/");
    },
    getDate() {
      var date = new Date();
      var y = date.getMonth() + 1;
      var d = date.getDate();
      var x = date.getDay();
      return y + "月" + d + "日 " + this.datex[x];
    },
    showPopup() {
      this.show = true;
    },
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.box {
  background-image: url("../assets/img/bg1.jpg");
  width: 100%;
  height: 100%;
  position: fixed;
  background-size: 100% 100%;
}
.d1 p,
.d1 span {
  color: #fff;
}
.d1 div {
  display: flex;
  justify-content: space-between;
}
.d1 div span {
  margin: 1rem 1rem;
  font-size: 1.5rem;
}
.d1 p {
  margin: 0.5rem 2rem;
}
.p1 {
  font-size: 2rem;
}
.p2 {
  font-size: 1.2rem;
}
.d3 {
  position: fixed;
  bottom: 0.5rem;
  right: 40%;
  left: 40%;
}
</style>